body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  display: flex;
  position: relative;
}

.minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotation;
  position: absolute;
  bottom: 50%;
}

.seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotation;
  position: absolute;
  bottom: 50%;
}

.center {
  color: #2c8000;
  background-color: #f6a603;
  border: 1vmin solid #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 48%;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .minutes {
  animation-duration: 600s;
}
