body {
  margin: 0;
}

.stopwatch {
  margin: auto;
  height: 80vmin;
  width: 80vmin;
  border: 1vmin dotted #000;
  border-radius: 50%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.minutes {
  height: 20vmin;
  width: 3vmin;
  background-color: #0700ff;
  position: absolute;
  bottom: 50%;
  animation: rotation;
  animation-duration: 3600s;
  animation-iteration-count: infinite;
  animation-timing-function: steps(60);
  transform-origin: bottom center;
}

.seconds {
  height: 38vmin;
  width: 1.5vmin;
  background-color: #2c8000;
  position: absolute;
  bottom: 50%;
  animation: rotation;
  animation-duration: 60s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transform-origin: bottom center;
}

.center {
  height: 5vmin;
  width: 5vmin;
  color: #2c8000;
  border: 1vmin solid #f6a603;
  border-radius: 50%;
  background-color: #f6a603;
  position: absolute;
  bottom: 48%;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .minutes {
  animation-duration: 600s;
}
